import YoutubeController from './controllers/YoutubeController';
import YoutubeModel from './models/YoutubeModel';
import YoutubeView from './views/YoutubeView';

const youModel = new YoutubeModel(fetch);
const youView = new YoutubeView(window);
const youController = new YoutubeController(youView, youModel);

window.addEventListener('load', youController.beginPage());

window.addEventListener('resize', youController.redrawOnresize);
