import './clipView.css';
import './searchBar.css';
import './nav.css';

export default class YoutubeView {
  constructor(element) {
    this.element = element;
    this.firstPageInPagingFrame = 1;
    this.enterSearch = null;
    this.goToNextPage = null;
    this.slideStart = null;
    this.slideEnd = null;
    this.openClip = null;
  }

  render() {
    const fragment = this.element.document.createDocumentFragment();
    const searchBar = this.element.document.createElement('div');
    const input = this.element.document.createElement('input');
    input.setAttribute('type', 'text');
    searchBar.classList.add('search-bar');
    input.setAttribute('id', 'input');
    input.classList.add('input-search');
    input.setAttribute('placeholder', 'What videos do you want to search?');
    input.addEventListener('keyup', this.enterSearch);
    searchBar.appendChild(input);
    fragment.appendChild(searchBar);

    const clips = this.element.document.createElement('section');
    clips.classList.add('clips');
    clips.setAttribute('id', 'clips');
    fragment.appendChild(clips);

    this.element.document.body.appendChild(fragment);

    const nav = this.element.document.createElement('section');
    nav.classList.add('nav');
    document.body.appendChild(nav);

    const prevPage = this.element.document.createElement('div');
    prevPage.classList.add('prev-page');
    prevPage.classList.add('tooltip');
    nav.appendChild(prevPage);

    const currentPage = this.element.document.createElement('div');
    currentPage.classList.add('curr-page');
    nav.appendChild(currentPage);

    const nextPage = this.element.document.createElement('div');
    nextPage.classList.add('next-page');
    nextPage.classList.add('tooltip');
    nav.appendChild(nextPage);

    nav.style.visibility = 'hidden';
  }

  drawStat(statArray) {
    const statistics = this.element.document.querySelector('.stat');
    for (let i = 0; i <= statArray.length - 1; i += 1) {
      statistics.innerHTML = `<p><b>views:</b> ${statArray[i].statistics}</p>`;
      statistics.classList.remove('stat');
    }
  }

  drawClips(clipsArray, currPage) {
    const clips = this.element.document.querySelector('#clips');
    clips.style.setProperty('--i', '0');
    clips.innerHTML = '';
    for (let i = 0; i <= clipsArray.length - 1; i += 1) {
      const li = this.element.document.createElement('div');
      li.classList.add('clips-item');
      li.classList.add('opacity');
      li.style.height = '100%';

      const fragment = this.element.document.createDocumentFragment();

      const img = this.element.document.createElement('img');
      img.setAttribute('src', `${clipsArray[i].thumbnails}`);
      img.setAttribute('alt', 'clip thumbnail');
      img.classList.add('img-of-clip');
      fragment.appendChild(img);

      const titleCont = this.element.document.createElement('div');
      titleCont.classList.add('title-cont');
      titleCont.addEventListener('mousedown', () => { this.openClip(clipsArray[i].id); });
      titleCont.addEventListener('touchend', () => { this.openClip(clipsArray[i].id); });
      titleCont.innerHTML = `<p>${clipsArray[i].title}</p>`;
      fragment.appendChild(titleCont);

      const chanelName = this.element.document.createElement('div');
      chanelName.classList.add('chanel-name');
      chanelName.innerHTML = `<p><b>chanel:</b> ${clipsArray[i].chanelName}</p>`;
      fragment.appendChild(chanelName);

      const date = this.element.document.createElement('div');
      date.classList.add('date');
      date.innerHTML = `<p><b>date:</b> ${clipsArray[i].publishedAt.slice(0, 10)}</p>`;
      fragment.appendChild(date);

      const statistics = this.element.document.createElement('div');
      statistics.classList.add('statistics');
      statistics.classList.add('stat');
      fragment.appendChild(statistics);

      const description = this.element.document.createElement('div');
      description.classList.add('description');
      description.innerHTML = `<p><b>descruption:</b> ${clipsArray[i].description}<span>        . ...                ,     . ,     </span></p>`;
      fragment.appendChild(description);

      li.appendChild(fragment);
      clips.appendChild(li);
    }
    const nav = this.element.document.querySelector('.nav');
    nav.style.visibility = '';
    const prevPage = this.element.document.querySelector('.prev-page');
    const currentPage = this.element.document.querySelector('.curr-page');
    const nextPage = this.element.document.querySelector('.next-page');

    const next = currPage + 1;
    const prev = currPage - 1;
    prevPage.style.visibility = '';
    prevPage.setAttribute('data-tooltip', prev);
    nextPage.setAttribute('data-tooltip', next);
    currentPage.innerHTML = `<span class="number">${currPage}</span>`;
    nav.addEventListener('click', this.goToNextPage);
    if (currPage === 1) {
      prevPage.style.visibility = 'hidden';
    }

    this.element.document.getElementById('clips').addEventListener('mousedown', this.slideStart);
    this.element.document.getElementById('clips').addEventListener('touchstart', this.slideStart);

    this.element.document.getElementById('clips').addEventListener('mousedown', (e) => { e.preventDefault(); });
    this.element.document.getElementById('clips').addEventListener('touchstart', (e) => { e.preventDefault(); });

    this.element.document.getElementById('clips').addEventListener('mouseup', this.slideEnd);
    this.element.document.getElementById('clips').addEventListener('touchend', this.slideEnd);
  }
}
