/* eslint-disable consistent-return */
export default class YoutubeModel {
  constructor(fetch) {
    this.fetch = fetch;
    this.nextPageToken = '';
    this.clips = [];
    this.statistics = [];
    this.endpointSearch = 'search';
    this.endpointVideos = 'videos';
    this.baseLink = 'https://www.googleapis.com/youtube/v3/';
    this.settings = {
      key: 'AIzaSyBWtxPSToPQnHGveWXFkzYD1ICAh8XJeV4',
      type: 'video',
      part: 'snippet',
      maxResults: 15,
    };
    this.settingsForStat = {
      key: 'AIzaSyBWtxPSToPQnHGveWXFkzYD1ICAh8XJeV4',
      part: 'statistics',
    };
  }

  makeUrl(value, pageToken, id) {
    if (id !== undefined) {
      let url = `${this.baseLink}${this.endpointVideos}?`;
      const urlOptions = { ...this.settingsForStat };
      // eslint-disable-next-line no-restricted-syntax
      for (const i in urlOptions) {
        if (Object.prototype.hasOwnProperty.call(urlOptions, i)) {
          url += `${i}=${urlOptions[i]}&`;
        }
      }
      url += `id=${id}`;
      return url.slice();
    }
    const urlOptions = { ...this.settings };
    let url = `${this.baseLink}${this.endpointSearch}?`;
    // eslint-disable-next-line no-restricted-syntax
    for (const i in urlOptions) {
      if (Object.prototype.hasOwnProperty.call(urlOptions, i)) {
        url += `${i}=${urlOptions[i]}&`;
      }
    }
    url += `q=${value}`;
    if (pageToken !== '') {
      url += `&pageToken=${pageToken}`;
    }
    return url.slice();
  }

  getClips(value, callback, countVideoPerPage, currPage) {
    let pageToken = '';
    if ((this.clips.length - countVideoPerPage * currPage) < 5) {
      pageToken = this.nextPageToken;
    }
    if (value !== '' || pageToken !== '') {
      fetch(this.makeUrl(value, pageToken))
        .then(res => res.json())
        .then((data) => {
          if (pageToken !== '') {
            this.clips = this.clips.concat(data.items);
            this.nextPageToken = data.nextPageToken;
          } else {
            this.clips = data.items;
            this.nextPageToken = data.nextPageToken;
          }
        })
        .then(() => {
          const arrayClips = this.clips.slice((currPage - 1) * countVideoPerPage,
            currPage * countVideoPerPage);
          callback(arrayClips, currPage);
        })
        // eslint-disable-next-line no-console
        .catch(err => console.log(err));
    } else {
      const arrayClips = this.clips.slice((currPage - 1) * countVideoPerPage,
        currPage * countVideoPerPage);
      callback(arrayClips, currPage);
    }
  }

  getStatistics(id, callback) {
    const url = this.makeUrl(this.endpointSearch, this.endpointVideos, id);
    fetch(url)
      .then(res => res.json())
      .then((data) => {
        this.statistics = data.items;
        callback(this.statistics);
      })
      .catch(err => console.log(err));
  }
}
