import '../views/clipView.css';
import '../views/searchBar.css';
import '../views/nav.css';

export default class YoutubeController {
  constructor(view, model) {
    this.model = model;
    this.view = view;
    this.countVideoPerPage = 4;
    this.currPage = 1;
    this.x0 = null;
    this.viewCount = '123';
    this.baseLinkForWatch = 'https://www.youtube.com/watch?v=';
  }

  beginPage() {
    this.view.enterSearch = this.enterSearch.bind(this);
    this.showClips = this.showClips.bind(this);
    this.videoPerPAgeFromExpanshion = this.videoPerPAgeFromExpanshion.bind(this);
    this.redrawOnresize = this.redrawOnresize.bind(this);
    this.view.slideStart = this.slideStart.bind(this);
    this.view.slideEnd = this.slideEnd.bind(this);
    this.view.openClip = this.openClip.bind(this);
    this.view.goToNextPage = this.goToNextPage.bind(this);
    this.view.render();
  }

  enterSearch(e) {
    this.videoPerPAgeFromExpanshion();
    this.currPage = 1;
    const { value } = document.getElementById('input');
    if (e.keyCode === 13) {
      this.model.getClips(value, this.showClips.bind(this), this.countVideoPerPage, this.currPage);
    }
  }

  async showClips(res, currPage) {
    const clipsArray = [];
    let i = 0;
    res.forEach((element) => {
      clipsArray[i] = {};
      clipsArray[i].thumbnails = element.snippet.thumbnails.medium.url;
      clipsArray[i].title = element.snippet.title;
      clipsArray[i].description = element.snippet.description;
      clipsArray[i].publishedAt = element.snippet.publishedAt;
      clipsArray[i].chanelName = element.snippet.channelTitle;
      clipsArray[i].id = element.id.videoId;
      setTimeout(this.model.getStatistics(element.id.videoId, this.showStatistics.bind(this)),
        15000);
      i += 1;
    });
    this.view.drawClips(clipsArray, currPage);
  }

  videoPerPAgeFromExpanshion() {
    const windowWidth = document.documentElement.clientWidth;
    this.countVideoPerPage = 4;
    if (windowWidth < 1450 || window.screen.width < 1450) {
      this.countVideoPerPage = 3;
    }
    if (windowWidth < 990 || window.screen.width < 990) {
      this.countVideoPerPage = 2;
    }
    if (windowWidth < 750 || window.screen.width < 750) {
      this.countVideoPerPage = 1;
    }
  }

  showStatistics(res) {
    const statArray = [];
    let i = 0;
    res.forEach((element) => {
      statArray[i] = {};
      statArray[i].statistics = element.statistics.viewCount;
      i += 1;
    });
    setTimeout(this.view.drawStat(statArray), 0);
  }

  goToNextPage(e) {
    if (e.target.classList.contains('next-page')) {
      this.currPage += 1;
    }
    if (e.target.classList.contains('prev-page')) {
      this.currPage -= 1;
    }
    this.model.getClips('', this.showClips.bind(this), this.countVideoPerPage, this.currPage);
  }

  slideStart(e) {
    const event = e.changedTouches ? e.changedTouches[0] : e;
    this.x0 = event.clientX;
    document.querySelector('#clips').style.cursor = 'grab';
  }

  redrawOnresize() {
    const firsElementOnPage = (this.currPage - 1) * this.countVideoPerPage + 1;
    this.videoPerPAgeFromExpanshion();
    this.currPage = Math.ceil(firsElementOnPage / this.countVideoPerPage);
    this.model.getClips('', this.showClips.bind(this), this.countVideoPerPage, this.currPage);
  }

  slideEnd(e) {
    const event = e.changedTouches ? e.changedTouches[0] : e;
    if ((this.x0 - event.clientX) < 0) {
      if (this.currPage !== 1) {
        document.querySelector('#clips').style.setProperty('--i', '100%');
        this.currPage -= 1;
        this.model.getClips('', this.showClips.bind(this), this.countVideoPerPage, this.currPage);
      }
    } else {
      document.querySelector('#clips').style.setProperty('--i', '-100%');
      this.currPage += 1;
      this.model.getClips('', this.showClips.bind(this), this.countVideoPerPage, this.currPage);
    }
    this.x0 = null;
  }

  openClip(id) {
    let url = this.baseLinkForWatch;
    url += `${id}`;
    window.open(url, '_blank');
  }
}
